	    			tag:"MT4",
	    			tab:false,
	    			desc:"-MT4 "
	    		},{
	    			tag:"MT4 ",
	    			tab:false,
	    			desc:"<br>1golday4Setup <br>2 3<br> 4 <br>5<br> 6 "
	    		},{
	    			tag:"/MT4 ",
	    			tab:false,
	    			desc:"1.<br> 1<br> 2 <br> 3 <br> 4Golday-DemoGolday-Live; <br> 5<br> 2.<br> 1 <br> 2X "
	    		},{
	    			tag:" ",
	    			tab:false,
	    			desc:" 1<br> 2<br> 3<br> 4<br> 5<br> 6<br> 7"
	    		},{
	    			tag:"?",
	    			tab:false,
	    			desc:"1 GOLDSILVER<br> 2  -  "
	    		},{
	    			tag:" ? ",
	    			tab:false,
	    			desc:"-<br> 1<br> 2<br> 3<br> 4  <br> 01. <br> 02. <br> 03. <br> <br> 1/<br> 230%"
	    		} 
	    	]
	    }
  	} 
}
</script> 
 


