<template>
	<div class="discount">
		<app-subHeader :subTitle="subTitle"></app-subHeader>
		<div class="container" v-for="(item,index) in discountList" :key="index"> 
			<img class="discountImg" :src="item.src" alt="">
			<p class="disName">{{item.title}} </p>
			<p class="disDesc">{{item.desc}}</p> 
		</div>
	</div> 
</template>

<script> 
import subHeader from '../header/SubHeader' 
import img from './img/customer_42.jpg'
export default {
	name: 'discount',
	data () {
	    return {
	    	subTitle:'',
	    	discountList:[
	    		{		
	    			src:img,
	    			title:'',
	    			desc:'1000100%'
	    		},
	    		{
	    			src:img,
	    			title:'',
	    			desc:'1000100%'
	    		},
	    		{
	    			src:img,
	    			title:'',
	    			desc:'1000100%'
	    		},
	    	]
	    }
  	},
  	components:{ 
  		"app-subHeader":subHeader,
  	},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped> 
	p{
		font-size:.35rem;
		line-height:.55rem;
	}
	.discountImg{
		width:100%; 
	}
	.disName{
		font-size: 0.3rem; 
		color: #333333; 
		line-height:.55rem;

	}
	.disDesc{
		font-size: 0.26rem;
		line-height:.4rem;
		text-indent:2em;
	}
</style>



