<template>
	<div class="investQue"> 
		<div class="container" v-for="(item,index) in basicList" :key="index" v-if="show"> 
			<p class="conTitle">{{item.title}}</p>
			<p class="conText">{{item.desc}}</p>
		</div> 

		<div class="lookMore"><span class="morePlus"> + </span> </div>
	</div> 
</template>

<script>  
export default {
	name: 'investQue',
	data () {
	    return { 
	    	show:true, 
	    	basicList:[
	    		{
	    			title:'111111',
	    			desc:''
	    		},
	    		{
	    			title:'K',
	    			desc:'KKK'
	    		},
	    		{
	    			title:'K',
	    			desc:' KKKK'
	    		},
	    	] 
	    }
  	}, 
}
</script>

 
 



