<template>
	<div class="investor">
		<section class="container">
			<p class="conText">      </p>
			<app-advantage ></app-advantage >
		</section>

		<section class="container" v-for="(item,index) in conList" :key="index">
			<h3 class="conTitle">{{item.tag}}</h3>
			<p class="conText">{{item.desc}}</p>
		</section>  
	</div> 
</template>

<script>
import advantage from './components/Advantage'
export default {
	name: 'investor', 
	data () {
	    return {
	    	conList:[
	    		{
	    			tag:"",
	    			desc:"**",
	    		},
	    		{
	    			tag:"",
	    			desc:"",
	    		},
	    		{
	    			tag:"",
	    			desc:" ",
	    		}  
	    	]
	    }
  	},
  	components:{
  		"app-advantage":advantage
  	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.container{
		width:100%;
		padding:.3rem;
		background:#fff;
		margin-top:.25rem;
	}	
	.video{
		width:100%;
	}
	.conTitle{
		font-size: 0.3rem;  
		color: #000;
		margin-bottom:.15rem;  
	}
	.conText{
		font-size: 0.26rem;  
		text-indent:2em;
		line-height:.45rem;
	}
	.titleC{
		font-size: 0.3rem; 
		color: #000;
		text-align: center;
		margin-bottom:.3rem;  
	}
</style>



