<template>
	<div class="deal">   
		<div class="box">
			<div class="group" v-for="(item,index) in conList" :key="index">
				<div class="flex headline" @click="item.tab = !item.tab">
					<span class="title"> <i>{{index+1}}</i> {{item.tag}}</span>
					<i class="arrow"></i>
				</div>
				<div class="detail" v-show="item.tab" v-html="item.desc">    </div>
			</div>
			
		</div>

	</div> 
</template>

<script>  
export default {
	name: 'deal',
  	data () {
	    return { 
	    	show:false,
	    	conList:[
	    		{
	    			tag:" ?",
	    			tab:false,
	    			desc:"/MT40.0520MF0.110"
	    		}, {
	    			tag:" ",
	    			tab:false,
	    			desc:"30%30%30%30% 60%  "
	    		},{
	    			tag:"",
	    			tab:false,
	    			desc:"/MT40.0520MF0.110 "
	    		},{
	    			tag:"  ",
	    			tab:false,
	    			desc:"  7:003:00()  7:004:00()   "
	    		}   
	    	]
	    }
  	} 
}
</script> 
 


