	</div> 
</template>

<script>  
export default {
	name: 'investQue',
	data () {
	    return { 
	    	show:true, 
	    	basicList:[
	    		{
	    			title:'111111',
	    			desc:''
	    		},
	    		{
	    			title:'K',
	    			desc:'KKK'
	    		},
	    		{
	    			title:'K',
	    			desc:' KKKK'
	    		},
	    	],
	    	advancedList:[
	    		{
	    			title:'222222222RSI',
	    			desc:''
	    		},
	    		{
	    			title:'',
	    			desc:''
	    		},
	    		{
	    			title:'',
	    			desc:'  '
	    		},
	    	]
	    }
  	}, 
}
</script>

 
 



